import React, { Component, Fragment } from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';
import { numberToMoney } from 'utils/formatUtil';
import { getChartCreditAmountByTrainType } from 'services/chartFinance';
import CustomizedAxisTick from './CustomizedAxisTick';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];
const CustomTooltip = ({ active, payload, label }) => {
  if (active && payload && payload.length && label) {
    return (
      <div className="recharts-default-tooltip" style={{margin: '0px', padding: '10px', backgroundColor: 'rgb(255, 255, 255)', border: '1px solid rgb(204, 204, 204)', whiteSpace: 'nowrap'}}>
        <p className="recharts-tooltip-label" style={{margin: '0px'}}>{label}</p>
        <ul className="recharts-tooltip-item-list" style={{padding: '0px', margin: '0px'}}>
          <li className="recharts-tooltip-item" style={{display: 'block', paddingTop: '4px', paddingBottom: '4px', color: 'rgb(0, 196, 159)'}}>
            <span className="recharts-tooltip-item-name">{label}</span>
            <span className="recharts-tooltip-item-separator"> : </span>
            <span className="recharts-tooltip-item-value">{numberToMoney(payload[0].value)}</span>
            <span className="recharts-tooltip-item-unit" />
          </li>
        </ul>
      </div>
    );
  }

  return null;
};

export default class CreditAmountBarChart extends Component {
  state = {
    creditAmountChartData: [],
    widthChart: 500,
  };

  componentDidMount = async () => {
    const widthChart = document.getElementsByClassName('utils__title')[0].offsetWidth-30;
    this.setState({
      widthChart,
    });
    const res = await getChartCreditAmountByTrainType();
    this.setState({
      creditAmountChartData: res.data.columns.map(item => {
        item.name = item.label;
        item[item.name] = item.value;
        return item;
      }),
    });
  };

  render() {
    const { creditAmountChartData, widthChart } = this.state;
    return (
      <Fragment>
        <BarChart
          width={widthChart}
          height={300}
          data={creditAmountChartData}
          margin={{
            top: 20,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name"  height={60} tick={<CustomizedAxisTick />} />
          <YAxis />
          <Tooltip content={<CustomTooltip />} />
          <Legend />
          {creditAmountChartData.map((data, i) => (
            <Bar key={data.name} stackId="a" dataKey={data.name} fill={COLORS[i]} />
          ))}
        </BarChart>
      </Fragment>
    );
  }
}



