import React from 'react';
import CreditPriceMultipleBarChart from '../creditPriceMultipleBarChart';
import CreditAmountBarChart from '../creditAmountBarChart';

export default function FinanceBeta() {
  return (
    <div>
      <div className="row">
        <div className="col-lg-12 col-xl-6">
          <div className="card">
            <div className="card-header">
              <div className="utils__title">
                <strong>Đơn giá tín chỉ theo đối tượng và hệ</strong>
              </div>
              <div className="utils__titleDescription" />
            </div>
            <div className="card-body">
              <div className="row">
                <div className="col-xl-12">
                  <CreditPriceMultipleBarChart />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-lg-12 col-xl-6">
          <div className="card">
            <div className="card-header">
              <div className="utils__title">
                <strong>Tiền học phí tín chỉ theo hệ</strong>
              </div>
              <div className="utils__titleDescription" />
            </div>
            <div className="card-body">
              <div className="row">
                <div className="col-xl-12">
                  <CreditAmountBarChart />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}



