import React, { Component, Fragment } from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';
import { numberToMoney } from 'utils/formatUtil';
import { getChartCreditPriceByFeeObjectNTrainType } from 'services/chartFinance';
import { makeid } from 'utils/functionUtil';
import CustomizedAxisTick from './CustomizedAxisTick';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042','#0123FE', '#12349F', '#FFB123', '#1F2032'];
const CustomTooltip = ({ active, payload, label }) => {
  if (active && payload && payload.length && label) {
    return (
      <div className="recharts-default-tooltip" style={{margin: '0px', padding: '10px', backgroundColor: 'rgb(255, 255, 255)', border: '1px solid rgb(204, 204, 204)', whiteSpace: 'nowrap'}}>
        <p className="recharts-tooltip-label" style={{margin: '0px'}}>{label}</p>
        <ul className="recharts-tooltip-item-list" style={{padding: '0px', margin: '0px'}}>
          { payload.length && payload.map((data) => (
            <li key={data.name} className="recharts-tooltip-item" style={{display: 'block', paddingTop: '4px', paddingBottom: '4px', color: 'rgb(0, 196, 159)'}}>
              <span className="recharts-tooltip-item-name">{data.name}</span>
              <span className="recharts-tooltip-item-separator"> : </span>
              <span className="recharts-tooltip-item-value">{numberToMoney(data.value)}</span>
              <span className="recharts-tooltip-item-unit" />
            </li>
            ))
          }
        </ul>
      </div>
    );
  }

  return null;
};

export default class CreditPriceMultipleBarChart extends Component {
  state = {
    creditPriceChartData: [],
    widthChart: 500,
  };

  componentDidMount = async () => {
    const widthChart = document.getElementsByClassName('utils__title')[0].offsetWidth-30;
    this.setState({
      widthChart,
    });
    const res = await getChartCreditPriceByFeeObjectNTrainType();
    const formatedData = res.data.labels.map((label, index) => {
      const item = { name: label };
      res.data.datas.forEach((data) => {
        item[data.label] = data.values[index];
      });
      return item;
    });
    this.setState({
      creditPriceChartData: formatedData,
    });
  };

  render() {
    const { creditPriceChartData, widthChart } = this.state;
    console.log('creditPriceChartData',creditPriceChartData);
    return (
      <Fragment>
        <BarChart
          width={widthChart}
          height={300}
          data={creditPriceChartData}
          margin={{
            top: 20,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid />
          <XAxis dataKey="name" height={60} tick={<CustomizedAxisTick />} />
          <YAxis />
          <Tooltip content={<CustomTooltip />} />
          <Legend />
          {Object.keys(creditPriceChartData[0] || {}).map((key, index) => {
            console.log('key',key);
            if (key === 'name') return null;
            return <Bar key={makeid(8)} dataKey={key} fill={COLORS[index]} />;
          })}
        </BarChart>
      </Fragment>
    );
  }
}



